import React from 'react';
import { useForm } from 'react-hook-form';
import { Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { ConfigSimplesNacionalSegregacao } from '../../../../models/dtos/mlf/configSimplesNacionalSegregacao';
import { SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/simplesNacionalSegregacaoEnum';

const emptyItem: ConfigSimplesNacionalSegregacao = {
  entity: 'configsimplesnacionalsegregacao',
  id: null,
  padrao: false,
  segregacao: null,
};

const ConfigSimplesNacionalSegregacaoEditView: React.FC<DetailEditViewFormProps<ConfigSimplesNacionalSegregacao>> = props => {
  const { errors, getValues, register, setValue, triggerValidation } = useForm<ConfigSimplesNacionalSegregacao>({
    defaultValues: props.selectedItem || emptyItem,
    mode: 'onBlur',
  });
  const formProps = {
    errors,
    getValues,
    register,
    setValue,
    triggerValidation,
  };

  const validateExtras = (formValues, currentValues, parentObject) => {
    const erros = [];

    if (parentObject.segregacoes) {
      const foundSegregacao = parentObject.segregacoes.find(seg => seg.segregacao === formValues.segregacao && seg.id !== formValues.id);
      if (foundSegregacao) {
        erros.push('Segregação já cadastrada para essa configuração.');
      }
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="segregacao"
            label="Segregação"
            formProps={formProps}
            isPrependLabel={true}
            placeholder="Informe a segregacao do simples nacional"
            inputDropDownProps={{ optionsProvider: SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES, enabled: false }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput type="switch" label="Padrão" id="padrao" name="padrao" innerRef={formProps.register} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ConfigSimplesNacionalSegregacaoEditView;
