import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import {
  boolFormatter,
  ColumnsProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  enumFormatter,
  getSelectedItem,
  InputGroupInline,
  InputType,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ConfigSimplesNacional } from '../../../../models/dtos/mlf/configSimplesNacional';
import { DIAS_ENUM_VALUES } from '../../../../models/enumerated/ger/diasEnum';
import { SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/simplesNacionalSegregacaoEnum';
import ConfigSimplesNacionalSegregacaoEditView from '../../../pages/mlf/configSimplesNacionalSegregacao/ConfigSimplesNacionalSegregacaoEditView';

interface ConfigSimplesNacionalProps extends EditViewProps<ConfigSimplesNacional> {}

const ConfigSimplesNacionalEditView: React.FC<ConfigSimplesNacionalProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigSimplesNacional>({ defaultValues: selectedItem, mode: 'onBlur' });

  const validateExtras = formValues => {
    const erros = [];
    if (formValues.notificarVencimento === true && !formValues.notificarVencimentoDias) {
      erros.push('Dias de Antecedência do vencimento - Campo não pode estar em branco.');
    }
    return erros;
  };

  const colunas: ColumnsProps[] = [
    {
      dataField: 'padrao',
      formatter: boolFormatter,
      text: 'Padrão',
    },
    {
      dataField: 'segregacao',
      formatExtraData: SIMPLES_NACIONAL_SEGREGACAO_ENUM_VALUES,
      formatter: enumFormatter,
      text: 'Segregação',
    },
  ];

  const segregacaoFormProps: DetailManagerFormProps = {
    columns: colunas,
    detailView: ConfigSimplesNacionalSegregacaoEditView,
    formProps,
    propName: 'segregacoes',
    title: 'Configuração Simples Nacional - Segregação',
  };

  return (
    <WindowComponent title="Configurações de Retenção Fonte">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Configuração de Retenção Fonte"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="valorFixoICMS"
              label="Valor Fixo ICMS (estado)"
              formProps={{ ...formProps, validation: { maxLength: 50 } }}
              isPrependLabel={true}
              inputProps={{
                title:
                  'O campo deve ser preenchido com o valor fixo de ICMS definido pelo ente federado. Não preencher com o valor da receita bruta.',
              }}
              placeholder="Informe o valor da fixo do ICMS"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="valorFixoISS"
              label="Valor Fixo ISS (estado)"
              formProps={{ ...formProps, validation: { maxLength: 50 } }}
              isPrependLabel={true}
              inputProps={{
                title: 'O campo deve ser preenchido com o valor fixo de ISS definido pelo ente federado. Não preencher com o valor da receita bruta.',
              }}
              placeholder="Informe o valor fixo do ISS"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diaVencimento"
              label="Dia de Vencimento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o dia de vencimento"
              inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração de Notificação</CardTitle>
                <Row>
                  <Col>
                    <CustomInput
                      id="notificarVencimento"
                      type="switch"
                      label="Notificar Vencimento de Imposto"
                      name="notificarVencimento"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="notificarVencimentoDias"
                      label="Dias de Antecedência do vencimento"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe com quantos de dias de antecedência gostaria de ser notificado"
                      inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES, enabled: false }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      id="notificarVencimentoPorEmail"
                      type="switch"
                      disabled={!formProps.getValues().notificarVencimento}
                      label="Notificar o cliente/responsável por Connect/email, sobre o vencimento do Imposto"
                      name="notificarVencimentoPorEmail"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <DetailManagerForm {...segregacaoFormProps} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigSimplesNacionalEditView;
